/*
@media (max-width: $break1) {}
@media (min-width: $break1 + 1px) and (max-width: $break2) {}
@media (min-width: $break2 + 1px) {}
*/
* {
  box-sizing: border-box; }

html {
  font-size: 13.2px;
  color: #343434; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* FONTS */
h1,
h2,
h3,
h4,
h5,
dt {
  font-family: "NIXGONB", sans-serif;
  margin-top: 0px; }

@media (max-width: 500px) {
  h2 {
    font-size: 2.13rem; } }

@media (min-width: 501px) and (max-width: 900px) {
  h2 {
    font-size: 2.33rem; } }

@media (min-width: 901px) {
  h2 {
    font-size: 3.33rem; } }

p,
li,
dd,
td,
span {
  font-family: "NIXGONL", sans-serif;
  letter-spacing: -0.45px; }
  @media (max-width: 500px) {
    p,
    li,
    dd,
    td,
    span {
      font-size: 1.2rem; } }
  @media (min-width: 501px) and (max-width: 900px) {
    p,
    li,
    dd,
    td,
    span {
      font-size: 1.33rem; } }
  @media (min-width: 901px) {
    p,
    li,
    dd,
    td,
    span {
      font-size: 1.46rem; } }

button,
th,
a {
  font-family: "NIXGONM", sans-serif; }
  @media (max-width: 500px) {
    button,
    th,
    a {
      font-size: 1.2rem; } }
  @media (min-width: 501px) and (max-width: 900px) {
    button,
    th,
    a {
      font-size: 1.33rem; } }
  @media (min-width: 901px) {
    button,
    th,
    a {
      font-size: 1.46rem; } }

/* SPACING */
@media (max-width: 500px) {
  .spacing-top {
    margin-top: 70px; } }

@media (min-width: 501px) and (max-width: 900px) {
  .spacing-top {
    margin-top: 100px; } }

@media (min-width: 901px) {
  .spacing-top {
    margin-top: 150px; } }

@media (max-width: 500px) {
  .spacing-btm {
    margin-bottom: 70px; } }

@media (min-width: 501px) and (max-width: 900px) {
  .spacing-btm {
    margin-bottom: 100px; } }

@media (min-width: 901px) {
  .spacing-btm {
    margin-bottom: 150px; } }

/* BUTTON */
button {
  outline: none;
  cursor: pointer; }

input[type="submit"] {
  outline: none;
  cursor: pointer; }

/* List */
ul.no-disc {
  list-style-type: none;
  padding: 0px; }
  ul.no-disc li {
    margin-bottom: 4px; }

.index-modal {
  position: fixed;
  box-sizing: border-box;
  padding: 10px;
  left: calc(50vw - 120px);
  right: calc(50vw - 120px);
  top: calc(50vh - 155px);
  bottom: calc(50vh - 155px);
  border: 1.5px solid #343434;
  background-color: white;
  border-radius: 10px; }
  .index-modal h3 {
    margin: 10px auto; }
  .index-modal button {
    margin-top: 10px;
    font-size: 1.3rem;
    letter-spacing: -0.1px; }
    .index-modal button:last-child {
      align-self: flex-end;
      margin-top: auto; }

.outer-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5); }

.modal-hide {
  display: none; }

.modal-show {
  display: flex;
  flex-direction: column; }
